import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class StaffSetupService {

  private staffSetupMatDrawer = new BehaviorSubject<any>(null);
  staffsetupmatdrawercast = this.staffSetupMatDrawer.asObservable();

  private staffSetupEntryRefresh = new BehaviorSubject<any>(false);
  staffsetupentryrefreshcast = this.staffSetupEntryRefresh.asObservable();


  constructor() { }

  getStaffSetupMatDrawer(){
    return this.staffSetupMatDrawer.value
  }

  setStaffSetupMatDrawer(value){
    this.staffSetupMatDrawer.next(value);
  }

  getStaffetupEntryRefresh(){
    return this.staffSetupEntryRefresh.value
  }

  setStaffSetupEntryRefresh(value){
    this.staffSetupEntryRefresh.next(value);
  }

}
